<template>
  <right-page :header="rightPageHeader" :hide-content-padding="true">
    <div slot="content">
      <div class="slds-form-element section">
        <label class="slds-form-element__label enabled-label">QR Code Enabled Objects</label>
        <div class="slds-form-element__control">
          <textarea class="slds-textarea textarea-container" v-model="enabledObjects.QRCodeRelatedObjectNames"></textarea>
          <div class="slds-form-element__help">Separate object API names with commas.</div>
        </div>
      </div>
      <footer class="slds-modal__footer">
        <button class="slds-button slds-button_neutral" :disabled="!hasChanged || isSaving" @click="cancel">Cancel</button>
        <button class="slds-button slds-button_brand" :disabled="!hasChanged || isSaving" @click="saveEnabledObj">Save</button>
      </footer>
    </div>
  </right-page>
</template>

<script>
import { getWeChatQRCodeEnabledObjects, saveWeChatQRCodeEnabledObjects } from '../api/charket-settings'

export default {
  name: 'EnabledObjects',
  data () {
    return {
      rightPageHeader: {
        label: 'CHARKET SETTINGS',
        name: 'Enabled Objects'
      },
      enabledObjects: {
        QRCodeRelatedObjectNames: ''
      },
      isSaving: false,
      originalData: null
    }
  },
  computed: {
    hasChanged () {
      return this.originalData !== this.enabledObjects.QRCodeRelatedObjectNames
    }
  },
  created () {
    this.fetchEnabledObj()
  },
  methods: {
    async fetchEnabledObj () {
      try {
        this.$bus.emit('show-loading')
        const data = await getWeChatQRCodeEnabledObjects()
        this.originalData = data.QRCodeRelatedObjectNames
        this.enabledObjects.QRCodeRelatedObjectNames = data.QRCodeRelatedObjectNames
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.$bus.emit('hide-loading')
      }
    },
    cancel () {
      this.enabledObjects.QRCodeRelatedObjectNames = this.originalData
    },
    async saveEnabledObj () {
      try {
        this.isSaving = true
        await saveWeChatQRCodeEnabledObjects(JSON.stringify(this.enabledObjects))
        this.$bus.emit('success', { content: 'Enabled Objects was saved.', type: 'success' })
        this.originalData = this.enabledObjects.QRCodeRelatedObjectNames
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.isSaving = false
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .section {
    padding: 20px 30px;
    .enabled-label, .slds-form-element__help {
      color: #706E6B;
      margin-top: -2px;
    }
    .textarea-container {
      width: 446px;
      min-height:60px;
    }
  }
</style>
